getDays(parkingDate){

this.date1 = new Date(parkingDate);
this.date2 = new Date();
//console.log("d1"+date1 +" d2"+ date2);
this.diffTime = Math.abs(this.date2 - this.date1);
this.days = Math.ceil(this.diffTime / (1000 * 60 * 60 * 24));
console.log(this.days);
}
